import React, {useState} from 'react';

const DaySchedulItem = ({idItem, time, lesson, teacher, classroom, darkMode}) => {
    time = time.split('-');
    const startTime = useState(time[0]);
    const endTime = useState(time[1]);
    return (
       <>
        <div key={idItem} id={idItem} className="d-flex">
            <h6 style={darkMode ? {color: '#dee1e4'} : {color: '#354766'}} className="mb-0 me-3">{startTime}<br/>{endTime}</h6>
            <h6 style={darkMode ? {color: '#dee1e4'} : {color: '#354766'}} className="mb-0">{lesson}<br/>
                <small style={darkMode ? {color: '#6c9596'} : {color: '#8392AB'}} className="font-weight-normal">{teacher}</small>
            </h6>
            {classroom
                ? <div className="bg-gradient-info ms-auto"
                       style={{borderRadius: '10px', padding: '0.9rem', height: 'max-content'}}>
                    {/* eslint-disable-next-line no-script-url,jsx-a11y/anchor-is-valid */}
                    <a className="ms-auto" title=" " style={{color: 'white'}}>
                        {classroom}
                    </a>
                </div>
                : <div></div>
            }
        </div>
        <hr className="horizontal dark"/>
      </>
    );
};

export default DaySchedulItem;