// eslint-disable-next-line no-unused-vars
import React, {useEffect, useState} from 'react';
import { Swiper, SwiperSlide } from "swiper/react";
import '../css/swiper.css';
import DaySchedulItem from "./DaySchedulItem";
// eslint-disable-next-line no-unused-vars

const ScheduleList = ({schedules, theme, darkMode, setDayWeekID}) => {
    let deyItemId = 0;
    let toDay = new Date().getDay();
    return (
        <Swiper
            onSwiper={(e) => {toDay == 0 ? e.activeIndex = 0 : e.activeIndex = (toDay - 1); setDayWeekID(e.activeIndex)}}
            onSlideChange={(e) => setDayWeekID(e.activeIndex)}
            className="slider"
        >
        {schedules.map((day, index) =>

        <SwiperSlide key={index} style={{padding: '2rem'}}>

            <div className="row mt-lg-n10 mt-md-n11 mt-n10">
                <div className="col-xl-4 col-lg-5 col-md-7 mx-auto">
                    <div className={`card z-index-0 ${theme}`}>
                        <input id='1' hidden defaultValue={day.title} />
                        <div className="card-body">
                            {/* eslint-disable-next-line array-callback-return */}
                            {day.pairs.map((sched) =>{
                                if(sched == null){
                            }else{
                                deyItemId = deyItemId + 1;
                                return(
                                    <>
                                    <DaySchedulItem darkMode={darkMode} idItem={deyItemId} time={sched.time} lesson={sched.lesson ? sched.lesson : sched.lesson1 && sched.lesson1 ? sched.lesson1 : sched.lesson2} teacher={sched.teacher ? sched.teacher : sched.teacher1 && sched.teacher1 ? sched.teacher1 : sched.teacher2} classroom={sched.classroom ? sched.classroom : sched.classroom1 && sched.classroom1 ? sched.classroom1 : null}/>
                                    </>
                                );
                            }}
                            )}
                        </div>
                    </div>
                </div>
            </div>
        </SwiperSlide>
            )}
        </Swiper>
    );
};

export default ScheduleList;